  function PassData() {
    this.msg = null;
    this.setmsg = function(msg) {
      return this.msg = msg;
    }
    this.getmsg = function() {
      return this.msg;
    }
  }


  function StatF() {
    return function(st) {
           if (st == 0) { return "NOT ACTIVE" }
      else if (st == 1) { return "ACTIVE"     }
      else if (st < 8 ) { return "SUSPENDED"  }
      else              { return "TERMINATED" }
    }
  }

